header {
  display: flex;
  justify-content: flex-end;
  width: 100%;
  position: fixed;
  z-index: 1000;
  top: 0;
  padding: 10px;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(0.75em);
}
.anchor-link-list {
  list-style-type: none;
  display: flex;
  justify-content: flex-end;
  gap: 20px;
}
.anchor-link-wrap {
  margin: 10px 0;
}
.anchor-link-item {
  color: white;
  text-transform: uppercase;
  font-size: 20px;
  font-weight: 300;
  text-decoration: none;
  transition: color 0.5s;
}
.anchor-link-item:hover {
  color: rgba(255, 0, 0, 0.8);
}
.burger {
  display: none;
  flex-direction: column;
  justify-content: center;
}
.line {
  width: 30px;
  transition: opacity 0.5s, transform 1s;
  height: 3px;
  background: white;
}
.line2 {
  margin: 5px 0;
}
.burger.active > .line1 {
  transform: rotate(45deg) translate(5px, 6px);
}
.burger.active > .line3 {
  transform: rotate(315deg) translate(5px, -6px);
}
.burger.active > .line2 {
  opacity: 0;
}
@media (max-width: 600px) {
  header {
    height: 64px;
    z-index: 1000;
  }
  .burger {
    display: flex;
  }
  .anchor-link-list {
    position: absolute;
    flex-direction: column;
    justify-content: center;
    background: rgb(37, 37, 37);
    right: 0;
    top: 64px;
    width: 100%;
    z-index: 1000;
    height: calc(100vh - 64px);
    text-align: center;
    transform: translateX(100%);
    transition: transform 1s;
  }
  .anchor-link-list.active {
    top: 64px;
    left: 0;
    height: calc(100vh - 64px);
    transform: translateX(0%);
  }
}
