#projects h2 {
  text-align: center;
  text-transform: uppercase;
  color: white;
  font-size: 50px;
  font-weight: 300;
  margin-bottom: 20px;
  background: hsla(0, 0%, 100%, 0.1);
  backdrop-filter: blur(5px);
}
.projects_wrap {
  max-width: 1000px;
  margin: 0 auto;
  flex-wrap: wrap;
  display: flex;
  gap: 20px;
  justify-content: center;
  margin-bottom: 20px;
}
.project_description_wrap {
  display: flex;
  flex-direction: column;
  height: 178px;
  padding: 5px;
  gap: 10px;
}
.project_item {
  color: white;
  width: 300px;
  min-height: 400px;
  background: hsla(0, 0%, 100%, 0.1);
  backdrop-filter: blur(10px);
  transition: transform 1s;
}
.project_item:hover {
  transform: translateY(-10px);
}
.project_image {
  width: 100%;

  filter: brightness(80%);

  height: 250px;
  object-fit: cover;
}
.project_title {
  font-weight: 700;
  letter-spacing: 1px;
}
.project_about {
  font-weight: 300;
  /* padding: 10px 0px; */
  margin-bottom: auto;
}
.project_lang {
  word-spacing: 3px;
  width: fit-content;
  font-weight: 300;
  padding: 5px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(0.75em);
}
.project_links {
  margin-top: auto;
  display: flex;
  justify-content: space-between;
  gap: 10px;
}
.project_links a {
  word-spacing: 3px;
  color: white;
  width: fit-content;
  text-decoration: none;
  padding: 5px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.15);
  position: relative;
}
.project_links a:after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 0px;
  height: 100%;
  background: rgba(246, 0, 0, 0.322);
  transition: width 1s;
}
.project_links a:hover:after {
  width: 100%;
}
.filter {
  display: flex;
  gap: 10px;
  margin: 0 auto;
  max-width: 1000px;
  margin-bottom: 20px;
}
.btn {
  word-spacing: 3px;
  width: fit-content;
  font-size: 20px;
  font-weight: 300;
  padding: 5px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(0.75em);
  color: white;
}
.btn.active {
  color: red;
}
@media (max-width: 608px) {
  .filter {
    overflow-x: scroll;
    padding-bottom: 5px;
  }
  .filter::-webkit-scrollbar {
    height: 10px;
  }
}
