.skills h2 {
  text-align: center;
  color: white;
  font-size: 50px;
  font-weight: 300;
  margin-bottom: 20px;
  background: hsla(0, 0%, 100%, 0.1);
  backdrop-filter: blur(5px);
}
.skills-wrap {
  margin: 0 auto;
  max-width: 1000px;
  padding: 30px;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  gap: 30px;
}
.skill-card {
  /* background-color: rgba(0, 0, 0, 0.3);
  background-blend-mode: overlay; */
  background: hsla(0, 0%, 100%, 0.1);
  backdrop-filter: blur(10px);
  width: 200px;
  height: 250px;
  position: relative;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.skill-shorthand {
  position: absolute;
  right: -20px;
  top: -20px;
  /* border-radius: 50%; */
  width: 50px;
  height: 50px;
  object-fit: cover;
}
.skill-title,
.skill-description {
  text-align: center;
  color: white;
}
.skill-title {
  font-size: 20px;
  font-weight: 400;
}
.skill-description {
  font-size: 16px;
  font-weight: 300;
  margin-top: 10px;
  padding: 0 20px;
}
