.hero {
  color: white;
  display: flex;
  justify-content: center;
  align-items: center;
  height: calc(100vh - 64px);
  margin-top: 64px;
}
.hero h1 {
  font-size: 3em;
  font-weight: 400;
  letter-spacing: 2px;
}
.hero-wrap {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 5px;
}
.fab {
  font-style: normal;
}
.name {
  font-size: 24px;
  text-transform: uppercase;
  word-spacing: 3px;
  font-weight: 300;
  padding: 5px;
  border: 1px solid white;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(0.75em);
}
.job {
  font-size: 20px;
  text-transform: uppercase;
  word-spacing: 3px;
  font-weight: 400;
  padding: 10px;
}
.socials {
  display: flex;
  list-style-type: none;
  gap: 20px;
}
.socials li a {
  color: white;
  font-size: 30px;
  text-decoration: none;
  transition: color 1s;
}
.socials li a:hover {
  color: rgba(255, 0, 0, 0.5);
}
.hero-wrap img {
  width: 150px;
  height: 150px;
  margin-bottom: 10px;
  border-radius: 50%;
  background: rgba(255, 255, 255, 0.15);
  backdrop-filter: blur(0.2em);
}
@media (max-width: 600px) {
  .hero {
    margin-top: 64px;
  }
}
